import React from 'react'
import { IconButton, Link, Paper, Typography } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf'
import Input from './Input'

import { parseBibFile, normalizeFieldValue } from 'bibtex'
import { publications } from '../content/publications'

const bibFile = parseBibFile(publications)


const styles = theme => ({
  publications: {
    margin: '5vw',
    width:'90vw',
    padding: theme.spacing(3),
    '@media (min-width:600px)': {
      padding: theme.spacing(3),
      width:'90vw',
    },
    '@media (min-width:800px)': {
      padding: theme.spacing(3),
      width:'75vw',
    },
    '@media (min-width:1000px)': {
      padding: theme.spacing(4),
      width:'60vw',
    },
    '@media (min-width:1200px)': {
      padding: theme.spacing(5),
      width:'50vw',
    },
  },
  title: {
    color: '#111',
    margin: theme.spacing(3, 0, 5, 0),
  },
  wrapper: {
    maxHeight: '50vh',
    overflow: 'auto',
    marginTop: theme.spacing(3),
  },
  publication: {
    margin: theme.spacing(5, 0),
    '& h5': {
      color: '#111',
    },
    '& p': {
      color: '#111',
    },
  },
})


class Publications extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      search: '',
    }
  }

  handleUpdate(search) {
    this.setState({search})
  }

  getAuthors(entry) {
    let authors = entry.getField("author")
    if ( !!authors ) {
      return authors.authors$.map((author, i) => (
        author.firstNames.concat(author.vons).concat(author.lastNames).concat(author.jrs).join(' ') + `${(i < authors.authors$.length - 1) ? ', ' : ''}`
      ))
    }
    return ''
  }

  normalizeValue(entry, field) {
    return (
      normalizeFieldValue(entry.getField(field)) || ''
    ).toString().toLowerCase()
  }

  renderPublications() {
    const {classes} = this.props
    const {search} = this.state
    const s = search.toLowerCase()
    return Object.keys(bibFile.entries$).filter(key => {
      let entry = bibFile.getEntry(key)
      let year = this.normalizeValue(entry, 'year')
      let title = this.normalizeValue(entry, 'title')
      let journal = this.normalizeValue(entry, 'journal')
      let booktitle = this.normalizeValue(entry, 'booktitle')
      let authors = this.getAuthors(entry).toString().toLowerCase()
      return year.includes(s) || title.includes(s) || journal.includes(s) || booktitle.includes(s)|| authors.includes(s)
    }).map(key => {
      let entry = bibFile.getEntry(key)
      let year = normalizeFieldValue(entry.getField('year'))
      let title = normalizeFieldValue(entry.getField('title'))
      let journal = normalizeFieldValue(entry.getField('journal'))
      let booktitle = normalizeFieldValue(entry.getField('booktitle'))
      let authors = this.getAuthors(entry)
      let url = normalizeFieldValue(entry.getField('url'))
      let type = entry.type.charAt(0).toUpperCase() + entry.type.slice(1)
      return (
        <div className={classes.publication} key={key}>
          <Typography component="h5" variant="h5">{title}</Typography>
          <Typography component="p" variant="body1">{authors}</Typography>
          {!!booktitle ? (
            <Typography component="p" variant="body1">
              {`${booktitle} ${!!year ? year : ''}`}
            </Typography>
          ) : (
            <Typography component="p" variant="body1">
              {`${type} published in ${!!journal ? `${journal}; ` : ''} ${!!year ? year : ''}`}
            </Typography>
          )}
          {!!url ? (
            <IconButton component={Link} href={url} target="_blank" rel="noopener noreferrer">
              <PictureAsPdfIcon />
            </IconButton>
          ) : null}
        </div>
      )
    })
  }

  render() {
    const {classes} = this.props
    const {search} = this.state
    return (
      <div id="publications" style={{paddingTop: '3vh'}}>
        <Paper elevation={3} className={classes.publications}>
          <Typography
            component="h4"
            variant="h4"
            className={classes.title}>
            Publications
          </Typography>
          <Input text={search} label={'search publications'} updateText={this.handleUpdate.bind(this)} />
          <div className={classes.wrapper}>
            {this.renderPublications()}
          </div>
        </Paper>
      </div>
    )
  }
}


export default withStyles(styles)(Publications)
